        methods:{
          // 
            changeMenu(){
                this.value = !this.value;
            },
            selectedCag(vId){
                this.isshow = vId;
            },
            handleChildCal (...payload){
                console.log(payload);

                switch (this.isshow) {
                case "commodity":
                    let [proType,proName] = payload;
                    console.log(proType, proName)
                    this.$axios('http://10.168.1.135:10086/product/selectAll',{
                    params:{
                        proType,
                        proName
                    }
                    }).then(res => {
                    console.log(res);
                    if(res.status == 200){
                        this.dialogTableVisible3 = true;
                        this.productData = res.data;
                    }
                    }).catch(res => {
                    console.log(res);
                    })
                    break;
                case "order":
                    
                    let [owner, consign, orderNumber] = payload; 

                    this.$axios('http://10.168.1.135:10086/order/selectAll',{
                    params:{
                        owner,
                        consign,
                        orderNumber
                    }
                    }).then(res => {
                        console.log(res);
                        if(res.status == 200){
                            this.dialogTableVisible2 = true;
                            this.orderData = res.data;
                            
                        }
                    }).catch(res =>{
                    console.log(res);
                    })
                break;
                default:
                    let [userName, nickName] = payload;
                    console.log(payload);
                    this.$axios('http://10.168.1.135:10086/user/selectAllUser',{
                    params:{
                        userName,
                        nickName
                    }
                    }).then(res => {
                    console.log(res);
                    if(res.status == 200){
                        this.dialogTableVisible1 = true;
                        this.userData = res.data;
                        console.log(this.userData);
                        
                    }
                    }).catch(res => {
                    console.log(res);
                    })
                    break;
                }
            },
            exportExcel (){
                console.log(this.selected1);
                let sheet = '';
                switch (this.selected1) {
                    case 'order':
                        sheet = '';
                        break;
                    case 'commodity':
                        sheet = '';
                        break;
                    default:
                        sheet = '';
                        break;
                }
                
                this.$axios({
                    url:'http://10.168.1.135:10086/exportExcel',
                    params:{
                        sheet
                    },
                    responseType: 'blob'
                }).then(res => {
                    console.log(res);

                    let fileName = window.decodeURI(res.headers['content-disposition'].split('=')[1]);
                    let link = document.createElement("a");
                    link.href = window.URL.createObjectURL(new Blob([res.data], {type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=utf-8"}));
                    link.target = "_blank";
                    link.download = fileName;
                    document.body.appendChild(link); 
                    link.click();
                    document.body.removeChild(link);
                }).catch(res => {
                    console.log(res);
                })
            },
            handleAdminMsg(){
                console.log(this.admform);
                this.dialogTableVisible4 = true;
                
            },
            deladmin() {
                this.logoImg = this.checkImgUrl;    
                console.log(this.logoImg);
                console.log(this.admform.admuser);
                console.log(this.admform.admpsw);
                this.$axios.get('http://10.168.1.135:10086/admin/update',{
                    params:{
                        name: this.admform.admuser,
                        passWord: this.admform.admpsw,
                        adminId: this.admform.adminId,
                        avatar: this.userImg
                    }
                }).then(res => {
                    console.log(res);
                    if(res.data == 1){
                        
                        this.dialogTableVisible4 = false;
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                        this.$route.query.name = this.admform.admuser
                    }else{
                        this.$message({
                            type: 'warning',
                            message: '!'
                        });
                    }
                }).catch(res => {
                    console.log(res);
                })
            },
            handleAvatarSuccess(res, file,filesList) {
                console.log(res);
                
                this.imageUrl = URL.createObjectURL(file.raw);
                let url = `http://10.168.1.135:10086/picture/${res}`;
                this.checkImgUrl = url;
                this.userImg = res;
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                // const isLt8M = file.size / 1024 / 1024 < 8;

                if (!isJPG) {
                this.$message.error(' JPG !');
                }
                // if (!isLt8M) {
                // this.$message.error(' 8MB!');
                // }
                return isJPG ;
            },
            getUserMsg(userId){
                console.log(userId);
                this.$axios.get('http://10.168.1.135:10086/user/selectByPrimaryKey',{
                    params:{
                        userId
                    }
                }).then(res => {
                    console.log(res);
                    this.form.userId = res.data.userId;
                    this.form.nickname = res.data.nickName;
                    this.form.name = res.data.userName;
                    this.form.password = res.data.passWord;
                    this.form.sex = res.data.sex;
                    this.dialogFormVisible = true;
                })
            },
            delUserMsg(data) {
            
                let index = data.$index;
                let username = data.row.username;

                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                this.$axios('http://10.168.1.135:10086/user/deleteUser',{
                    params:{
                    userName:username
                    }
                }).then(res => {
                    console.log();
                    if(res.data == 1){
                        this.tableData.splice(index, 1);
                        this.$message({
                        type: 'success',
                        message: '!'
                    });
                }else{
                    this.$message({
                        type: 'warning',
                        message: '!'
                    });
                }
                }).catch(res => {
                    console.log(res);
                })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });          
                });

            },
            getOrderMsg(orderId) {
                console.log(orderId);
                this.dialogFormVisible = true;
                this.$axios('http://10.168.1.135:10086/order/selectByPrimaryKey',{
                    params:{
                    orderId
                    }
                }).then(res => {
                    console.log(res);
                    console.log(this.form);
                    this.form.orderId = res.data.orderId;
                    this.payArr = [];
                    this.form.productList = [];
                    this.form.owner = res.data.owner;
                    this.form.createTime = res.data.createTime;
                    this.form.consign = res.data.consign;
                    this.form.consignPhone = res.data.consignPhone;
                    this.form.consignAddress = res.data.consignAddress;
                    this.payArr.push(res.data.payMethod);
                    this.form.productList = res.data.productList;
                    this.form.orderNumber = res.data.orderNumber;
                    this.form.nickName = res.data.nickName;
                }).catch(res => {
                    console.log(res);
                })
            },
            delOrderMsg(data) {
            
                let [orderId, index] = [data.row.orderId, data.$index];

                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$axios('http://10.168.1.135:10086/order/deleteByPrimaryKey',{
                    params:{
                        orderId
                    }
                    }).then(res => {
                        console.log(res);
                        if(res.data == 1){
                            this.tableData.splice(index, 1);
                            this.$message({
                                type: 'success',
                                message: '!'
                            });
                            }else{
                            this.$message({
                                type: 'warning',
                                message: '!'
                            });
                        }
                    }).catch(res => {
                    console.log(res);
                    })
                }).catch(res =>{
                    console.log(res);
                })
            },
            getProductMsg(proId){
                console.log(proId);
                this.$axios.get('http://10.168.1.135:10086/product/selectByPrimaryKey',{
                    params: {
                        proId
                    }
                }).then(res => {
                console.log(res);
                    let [conf, color, meal] = [res.data.conf, res.data.color, res.data.meal];
                
                    let [configars, colors, meals] = [[],[],[]];

                    this.dialogFormVisible = true;
                    // this.value = res.data.proID;
                    this.form.proId = res.data.proId;
                    this.form.name = res.data.name;
                    this.form.originalPrice = res.data.originalPrice;
                    this.form.currentPrice = res.data.currentPrice;
                    this.form.redNote = res.data.redNote;
                    this.form.grayNote = res.data.grayNote;
                    this.form.proType = res.data.proType;
                    

                    switch (res.data.proType) {
                        case 'shouhuan':
                            this.value = 2;
                            break;
                        case 'jiadian':
                            this.value = 3;
                            break;
                        default:
                            this.value = 1;
                            break;
                    }
                    
                    for (let i = 0; i < conf.length; i++) {
                        configars.push(conf[i].conf)
                    }

                    for (let i = 0; i < color.length; i++) {
                        colors.push(color[i].color)
                    }

                    for (let i = 0; i < meal.length; i++) {
                        meals.push(meal[i].meal)
                    }

                    this.colorarr = [];
                    this.confarr = [];
                    this.mealarr = [];
                    this.form.color = [];
                    this.form.conf = [];
                    this.form.meal = [];

                    this.colorarr = Array.from(colors);
                    this.confarr = Array.from(configars);
                    this.mealarr = Array.from(meals);
                    
                    
                    for (let i = 0; i < this.colorarr.length; i++) {
                        let obj= {};
                        obj['color'] = this.colorarr[i];
                        this.form.color.push(obj);
                    }

                    for (let i = 0; i < this.confarr.length; i++) {
                        let obj= {};
                        obj['conf'] = this.confarr[i];
                        this.form.conf.push(obj);
                    }

                    for (let i = 0; i < this.mealarr.length; i++) {
                        let obj= {};
                        obj['meal'] = this.mealarr[i];
                        this.form.meal.push(obj);
                    }
                    console.log(this.form);

                }).catch(res => {
                console.log(res);
                })
            },
            delProductMsg (data) {
                let index = data.$index;
                let productid = data.row.proID
                console.log(productid);
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                this.$axios('http://10.168.1.135:10086/product/deleteProduct',{
                    params:{
                        productId: productid
                    }
                }).then(res => {
                    console.log(res);
                    if(res.data == 1){
                        this.tableData.splice(index, 1);
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }else{
                        this.$message({
                            type: 'warning',
                            message: '!'
                        });
                    }
                }).catch(res => {
                    console.log(res);
                })
                }).catch(res =>{
                    console.log(res);
                })
            
            },
            drpOut() {
                // console.log()
                this.$router.push('/');
            }
        },
        watch:{
            value:function(newValue,oldValue){
                this.value = newValue;
                this.$emit("changeMenu",this.value)
            }
        },
        filters:{
            dataFormat: function(data){
                let d = new Date(data);
                let str = '';
                let [day, month, year] = [d.getDate(), parseInt(d.getMonth())+1, d.getFullYear()]
                str = year + '' + month + '' + day + '';
                return str;
            },
            pswFormat: function(data){
                return '******';
            },
            protypeFm:function(data){
                switch (data) {
                    case 'jiadian':
                        return '';
                        break;
                    case 'shouhuan':
                        return '';
                        break;
                    default:
                        return '';
                        break;
                }
            }
    },
  }
</script>
<style scoped>
  .header{
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .inputSearch{
    width: 100%;
  }
  .userLogo img{
    width: 40px;
    height: 40px;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    -webkit-border-radius:50%;
    -moz-border-radius:50%;
    margin-right: 5px;
  }
  .userLogo{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 20px;
  }
  .userMessage{
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .userMessage span{
    margin: 10px;
    cursor: pointer;
    color: #fff;
    font-size: 15px;
  }
  .userMessage span:hover{
    color: #125b70;
  }

  .lange{
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    cursor: pointer;
    background: #fff;
    border-radius: 50%;
    margin: 0 10px;
  }
  .lange:hover{
    background: #f3f3f3;
  }


  .userSel{
    line-height: 30px;
  }
  .userSel div{
    cursor: pointer;
    color: #8d9a9c;
  }
  .userSel div:hover{
    color: #125b70;
  }

  @media screen and (max-width: 900px){
    .inputBox{
      display: none;
    }
    .spanBox{
      display: none;
    }
  }
  
  .el-button--info{
    color: #909399;
    background-color: #fff;
    border-color: #fff;
  }
  .smallbox{
    padding-right: 20%;
  }
  .excelSend{
      outline: none;
  }

   .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    border: solid 1px #ccc;
    border-radius: 50%;
    width: 70px;
    height: 70px;
    line-height: 70px;
    text-align: center;
  }
  .avatar {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    display: block;
  }
</style>



